/* styles.css */

body {
  background-color: #f5f5f5; /* Светло-серый цвет фона */
  margin: 0;
}

.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1 {
  color: #333;
  font-size: 20px;
}

#map-container {
  width: 100%;
  height: 400px;
  margin-bottom: 20px;
}

button {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  margin-top: 20px;
  border-radius: 20px;
}

button:hover {
  background-color: #45a049;
}
